import { Component, Prop, Event, EventEmitter, h } from '@stencil/core';

@Component({
  tag: 'range-input',
  styleUrl: 'range-input.css',
  shadow: true,
})
export class RangeInput {
  /**
   * The name of the input field.
   */
  @Prop() name!: string;

  /**
   * The label for the input field.
   */
  @Prop() label!: string;

  /**
   * The current value of the range input.
   */
  @Prop({ mutable: true, reflect: true }) value: number = 0;

  /**
   * The minimum allowed value.
   */
  @Prop() min: number = 0;

  /**
   * The maximum allowed value.
   */
  @Prop() max: number = 100;

  /**
   * The step interval for the range.
   */
  @Prop() step: number = 1;

  /**
   * Whether the input is disabled.
   */
  @Prop() disabled: boolean = false;

  /**
   * Event emitted when the input value changes.
   */
  @Event() myChange: EventEmitter<number> | undefined;

  private handleChange = (event: Event) => {
    if (this.disabled) {
      return;
    }

    const input = event.target as HTMLInputElement;
    this.value = parseFloat(input.value);
    this.myChange?.emit(this.value);
  };

  render() {
    const range = this.max - this.min;
    const fillPercentage = range > 0 ? ((this.value - this.min) / range) * 100 : 0;

    return (
      <div
        class={{ 'range-wrapper': true, 'range-wrapper--disabled': this.disabled }}
        style={{ '--fill-percentage': `${fillPercentage}%` }}
      >
        <label htmlFor={this.name} class="range-label">
          {this.label}
          <span class="range-value">{this.value}</span>
        </label>
        <input
          type="range"
          id={this.name}
          name={this.name}
          value={this.value}
          min={this.min}
          max={this.max}
          step={this.step}
          disabled={this.disabled}
          onChange={this.handleChange}
          class="range-field"
        />
      </div>
    );
  }
}
